<template>
  <div id="root-div">
    <VHeader></VHeader>
    <el-container>
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu router :default-active="activeRouter">
          <el-submenu index="1">
            <template slot="title"><i class="el-icon-message"></i></template>
            <el-menu-item-group>
              <template slot="title"></template>
              <el-menu-item index="/manage/list"></el-menu-item>
              <el-menu-item index="/manage/add"></el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group>
              <template slot="title"></template>
              <el-menu-item index="1-3"></el-menu-item>
              <el-menu-item index="1-4"></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-menu"></i></template>
            <el-menu-item-group>
              <template slot="title"></template>
              <el-menu-item index="2-1"></el-menu-item>
              <el-menu-item index="2-2"></el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="2">
              <template slot="title"></template>
              <el-menu-item index="2-3"></el-menu-item>
              <el-menu-item index="2-4"></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title"><i class="el-icon-setting"></i></template>
            <el-menu-item-group>
              <template slot="title"></template>
              <el-menu-item index="3-1"></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <transition name="fade" mode="out-in" :duration="100" appear>
          <keep-alive>
            <router-view></router-view>
          </keep-alive>
        </transition>
      </el-main>
    </el-container>
  </div>
</template>

<script type="text/ecmascript-6">
   export default {
     data () {
       return {
         activeRouter: this.$route.path
       }
     }
   }
</script>
<style lang="stylus">
  #root-div
    height:100%
    >.el-container
      height:100%
  .el-aside
    color: #333
  .router-link{
    display:block;
    width: 100%;
    height: 100%;
  }

</style>



