<template>
  <div id="map"></div>
</template>

<script>
import ol from 'openlayers'
export default {
  mounted () {
    let map = new ol.Map({
      // 
      layers: [
        // Open Street Map
        new ol.layer.Tile({source: new ol.source.OSM()})
      ],
      // 
      view: new ol.View({
        center: [0, 0],    // 00
        zoom: 2            // 2
      }),
      // idmapdiv
      target: 'map'
    })
  }
}
</script>

<style >
#map{
  width: 100%;
  height: 100%;
}
</style>





